@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

.about-us-container{
    display: flex;
    padding: 0 50px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.about-1{
    flex: 1;
}

.about-1 h1{
    font-family: 'Montserrat', sans-serif;
    color: #303549;
    font-weight: 700;
}

.about-1 p{
    font-family: 'Montserrat', sans-serif;
    color: #303549;
    font-weight: 400;
}

.about-1 p{
    font-family: 'Montserrat', sans-serif;
    color: #303549;
    font-weight: 400;
    line-height: 2;
}

.about-us-buttons{
    width: 100%;
    display: flex;;
    gap: 10px;
    font-family: 'Montserrat', sans-serif;
    border: none;
}

.about-us-buttons button{
    padding: 1rem 2rem;
    border-radius: 5px;
    background-color: #8E06C2;
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: none;
    font-weight: bold;
    cursor: pointer;
}

.about-2{
    flex: 1;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-2 img{
    width: 100%;
}

.about-us-banner{
    background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692439511/AboutPage/about-us-banner_1_1_1_1_1_blfuny.webp");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    width: 100%;
    height: 100vh;
}

@media (max-width: 767px) {
    .about-us-banner{
        height: 30vh;
    }

    .about-us-container{
        flex-direction: column;
        padding: 0 25px;
        gap: 2.5vh;
    }

    .about-us-buttons{
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

}